





























































var userLoginInfo;
export default {
	data() {
		return {
			loadMoreText: "加载中...",
			showLoadMore: false,
			// 账户余额
			remainder: '',
			moenyList: [],
			walletList: [], //收益
			page: 1,
			photo: '',
			nickname: '',
			profit:true
			
		}
	},
	onLoad() {
		
	},
	// 监听页面卸载
	onUnload() {
		this.moenyList = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		this.getDetailed();
	},
	onShow() {
		uni.showLoading({
			title: '加载中',
		});
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		this.photo = userLoginInfo.photo;
		this.nickname = userLoginInfo.nickname;
		this.getWallet();
		this.getDetailed();
		this.getRemainder();
	},
	methods: {
		// 获取提现状态
		goExtract: function() {
			uni.navigateTo({
				url: "/pages/extract/extract_list"
			})
		},
		// 获取用户余额
		getRemainder() {
			uni.request({
				url: this.apiUrl + 'user/getUser',
				method: 'GET',
				data: {
					uid: userLoginInfo.id
				},
				success: (res) => {
					if (res.data.code == 0) {
						this.remainder = res.data.data.price;
						// console.log(this.remainder);
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: function() {
					uni.hideLoading();
				}

			});
		},
		/* 收益 */
		getWallet() {
			uni.request({
				url: this.apiUrl + 'user/getSaleCount',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
				},
				success: res => {
					// console.log(res)
					if (Array.isArray(res.data.data) && res.data.data.length === 0) {
						return;
					}
					if (res.data.code == 0) {
						this.walletList = res.data.data;
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: function() {
					uni.hideLoading();
				}
			});
		},
		//调取明细列表
		getDetailed() {
			uni.request({
				url: this.apiUrl + 'user/getSaleCashFlowList',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					page: this.page
				},
				success: (result) => {
					if (result.data.code == 0) {
						if (Array.isArray(result.data.data.data) && result.data.data.data.length === 0) {
							this.loadMoreText = "没有更多数据了!"
							return;
						}
						// 当用户还无任何收入明细时；
						if(this.page == 1 && result.data.data.data.length === 0){
							this.profit = true;
						}else{
							this.profit = false;
						}

						if (result.data.data.data.length < 5) {
							this.loadMoreText = "没有更多数据了!"
						}
						this.showLoadMore = true;
						this.page++;
						result.data.data.data.forEach((money) => {
							this.moenyList.push(money);
						});
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: function() {
					uni.hideLoading();
				}

			});
		}

	}



}
